*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  max-width: 100%;
  font-family: Lemonada, cursive;
  font-weight: 400;
  line-height: 1;
  position: relative;
}

.container {
  grid-template-columns: 57rem minmax(0, 1fr);
  height: 100vh;
  display: grid;
}

.sidebar {
  background-color: #444b54;
  flex-direction: column;
  grid-row: 1 / -1;
  align-items: center;
  gap: 3.6rem;
  padding: 1.2rem 2.4rem;
  display: flex;
}

@keyframes skewInLeft {
  from {
    opacity: 0;
    transform: translateX(-100%)skewX(-40deg);
  }

  70% {
    transform: skewX(-30deg);
  }
}

.sidebar .header {
  align-items: center;
  gap: 1.2rem;
  animation: 1.2s ease-in-out skewInLeft;
  display: flex;
}

.sidebar .header__name {
  color: #7dcf9b;
}

.sidebar .header__img {
  max-width: 100%;
}

@keyframes pushReleaseFromBottom {
  from {
    transform: translateY(100%);
  }

  30% {
    transform: translateY(-100px);
  }
}

h1 {
  font-size: 3.6rem;
  font-weight: 600;
}

html body .hidden {
  display: none;
}

.btn {
  color: #7dcf9b;
  cursor: pointer;
  background-color: #000;
  border: none;
  border-radius: 2.4rem;
  padding: 1rem 3.6rem;
  font-size: 1.8rem;
  font-weight: 600;
  transition: all .3s;
}

.btn:hover {
  cursor: pointer;
  color: #fff;
  background: linear-gradient(135deg, #7dcf9b, #7dcf9b);
  transform: scale(1.05);
  box-shadow: 0 8px 15px #0003;
}

.btn--reverse {
  color: #fff;
  background-color: #7dcf9b;
  animation: 1.2s ease-in-out pushReleaseFromBottom;
}

.btn--reverse:hover {
  color: #7dcf9b;
  background: linear-gradient(135deg, #444b54, #444b54);
}

.btn--small {
  background-color: #000;
  padding: .6rem 1.8rem;
  font-size: 1.6rem;
}

.btn--small:hover {
  cursor: pointer;
  color: #fff;
  transform: scale(1.05);
  box-shadow: 0 8px 15px #0003;
}

@keyframes blurInTop {
  from {
    filter: blur(20px);
    opacity: 0;
    transform: translateY(100%);
  }
}

@keyframes coolHorizontalShake {
  3%, 21%, 39%, 57%, 74%, 92% {
    transform: translateX(5px);
  }

  6%, 24%, 42%, 60%, 77%, 95% {
    transform: translateX(3px);
  }

  9%, 27%, 45%, 63%, 80%, 98% {
    transform: translateX(-5px);
  }
}

@keyframes moveDown {
  0% {
    transform: translateY(25%);
  }

  50% {
    transform: translateY(50%);
  }

  100% {
    transform: translateY(100%);
  }
}

@keyframes blurOutTop {
  85%, to {
    filter: blur(20px);
    transform: translateY(100%);
  }

  to {
    opacity: 0;
  }
}

.move {
  animation: 3s ease-in-out infinite moveDown;
}

.blurOutTop {
  animation: 1.2s ease-in-out infinite blurOutTop;
}

.coolHorizontalShake {
  animation: 1.2s infinite coolHorizontalShake;
}

form {
  background-color: #7dcf9b;
  border-radius: .8rem;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  gap: 1.2rem;
  width: 100%;
  height: 13rem;
